import React, { useState } from 'react'
import { Document, Page, pdfjs} from 'react-pdf';
import styled from 'styled-components'

import { SizeMe } from 'react-sizeme'

import GlobalWrapper from 'components/global-wrapper'
import media from 'utils/media-queries'
import Link from 'gatsby-link'


import Header from 'components/article/header'
import CoverImage from 'components/article/coverimage'
import ContentWrapper from 'components/article/contentwrapper'
import Footer from 'components/footer'
import FullSizeImage from 'components/article/fullsizeimage'
import LargeImage from 'components/article/largeimage'

import CoverSrc from 'img/articles/academia/pandemic/00_intro_image_3.jpg'
import ContentPDF from 'img/articles/academia/pandemic/03_pandemic_disruption.pdf'

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const Section = styled.section`
  margin: 64px 0;
  ${media.sm`
    margin: 0;
  `}
`

const Content = styled.div`
  canvas {
    width: 100% !important;
    height: auto !important;
  }
  max-width: 100%;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  p + h3 {
    margin-top: 42px;
  }
  padding: 0 0 0 0;
  ${media.lg`
    padding-right: 0;
  `}
  ${media.xs`
  max-width: 700px;
  padding: 0 0 0 0;
  `}
`

const EmbedContainer = styled.div`
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  width: 100%;
  height: auto;
  margin-top: 32px;
  margin-bottom: 32px;
  @media (min-width: 1000px) {
    width: 1000px;
    height: 562px;
    padding-bottom: 0px;
    margin-left: auto;
    margin-right: auto;
  }
`

const Video = styled.iframe`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
`

const ImageGrid = styled.div`
  background: #f2f2f2;
  max-width: 100%;
  padding: 40px;
  margin: 32px 0;
  text-align: center;
  ${media.lg`
    padding: 32px;
    margin: 0 0 24px 0;
  `}
  ${media.md`
    margin: 0;
    margin: 0 0 16px 0;
  `}
  ${media.sm`
    padding: 16px;
  `}
`

const Img = styled.img`
  max-width: calc(100% - 96px);
  max-height: 300px;
  margin: 8px;
  display: inline-block;
  ${media.xl`
    max-height: 240px;
  `}
  ${media.lg`
    max-height: 200px;
  `}
  ${media.md`
    max-height: 120px;
    margin: 4px;
  `}
  ${media.sm`
    max-height: 80px;
    margin: 2px;
  `}
`

const LifeTime = () => {
  const [numPages, setNumPages] = useState(null);
  return (
    <GlobalWrapper>
      <Header title="Pandemic Disruption" lead="De-folding fragile food system landscapes of post-fordist  mode of production & consumption" />
      <CoverImage src={CoverSrc} focusX={'20%'} focusY={'100%'} />
      <Section>
        <ContentWrapper>
          <h2>Pandemic Disruption</h2>
          <h3>Singapore</h3>
          <p>
          This studio expands the traditional notions of productive landscapes
          by tracing the <b>life cycles of critical products in the wake of the
          COVID-19 pandemic</b>. By <b>mapping the spaces</b> of material extraction to 
          final consumption, we gain a greater picture of the resources, 
          infrastructure, and labor that shape the landscapes supporting our 
          essential needs. A few questions emerge: What are the <b>social, economic, 
          and environmental consequences of a global supply chain heavily dependent 
          on China and Southeast Asia</b>? What <b>vulnerabilities has the COVID-19 
          pandemic exposed, and where are they located</b>?
          </p>
          <p>
          The COVID-19 Pandemic has significantly disrupted the global economy, 
          especially the <b>post-Fordist modes of manufacturing that rely heavily 
          on just-in-time logistics, long supply chains, and international 
          divisions of labor</b>. This has been especially evident in a few critical 
          sectors such as medical equipment, food, essential consumer goods, 
          and even construction. The <b>project explores supply chain traces</b> of 
          one of the key non-durable edible product - shell eggs - and attempts 
          to <b>identify key vulnerability points</b> in the global supply chain and 
          <b>understanding landscape impact</b>.   
          </p>
        </ContentWrapper>
      </Section>
      <Content>
      <SizeMe
        monitorHeight
        refreshRate={128}
        refreshMode={"debounce"}
        render={({ size }) => (
        <div>
          <Document 
            file={ContentPDF} 
            onLoadSuccess={({ numPages })=>setNumPages(numPages)}>
            {Array.apply(null, Array(numPages))
                .map((x, i)=>i+1)
                .map(page => <Page pageNumber={page} width={size.width} renderMode="png" scale={1.0}/>)}
          </Document>
        </div>
      )} />
      </Content>
      <Footer />
    </GlobalWrapper>
  )
}

export default LifeTime
